import { Box, Stack, Typography, Button } from '@mui/material';import errorImg from 'src/images/routesError/errorImg.png';export const ErrorPage = () => {    return (        <Stack spacing="44px" sx={{ pt: '228px' }} alignItems="center" justifyContent="center">            <Box component="img" src={errorImg} sx={{ width: '168px', height: '176px', mb: '20px' }} />            <Stack spacing="24px" sx={{ maxWidth: '890px' }} alignItems="center">                <Typography                    sx={{                        fontSize: { laptop: '36px', tablet: '24px' },                        lineHeight: { laptop: '48px', tablet: '32px' },                        fontWeight: '600',                    }}                >                    ,  -                   </Typography>            </Stack>            <Button href="/" variant="primary" sx={{ width: '342px' }}>                             </Button>        </Stack>    );};